
<template>
  <div v-if="toy" class="toy-details">
    <section class="toy-data-show">
      <h1>welcome to Toy details page</h1>
      <h3>{{ toyToShow._id }}</h3>
      <h3>{{ toyToShow.name }}</h3>
      <h3>{{ toyToShow.createdAt }}</h3>
      <h3>{{ toyToShow.inStock }}</h3>
      <h3>{{ toyToShow.price }}</h3>
      <h3>{{ toyToShow.type }}</h3>
      <router-link to="/toy">Back</router-link>
    </section>

    <section>
      <h3>Add review:</h3>
      <form @submit.prevent="addReview">
        <label for="txt">review:</label>
        <input type="text" v-model="review.txt" />
        <button>Add</button>
      </form>

      <ul class="review-list">
        <li v-for="review in reviewsToShow" :key="review._id">
          <h3>{{ review.content }} By: {{ review.user.fullname }}</h3>

          <!-- <div class="btns-container">
            <button @click="remove(toy._id)">X</button>
            <router-link :to="'/details/' + toy._id">Details</router-link>
            <router-link :to="'/edit/' + toy._id">Edit</router-link>
          </div> -->
        </li>
      </ul>
    </section>

    <section class="chat-room">
      <chat-Page :toy="toy"></chat-Page>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import { toyService } from "../services/toy.service.js";
import { reviewService } from "../services/review.service.js";
import chatPage from "./chat-page";

export default {
  name: "toy-details",
  data() {
    return {
      toy: null,
      review: {
        txt: "",
      },
      reviews: [],
    };
  },
  computed: {
    toyToShow() {
      return this.toy;
    },
    reviewsToShow() {
      return this.reviews;
    },
  },
  methods: {
    async loadToy() {
      console.log("try get toy");
      const id = this.$route.params.toyId;
      await toyService.getById(id).then((toy) => (this.toy = toy));
      let reviews = await reviewService.query({ toyId: id });
      console.log("we got load review in details comp", reviews);
      this.reviews = reviews;
    },
    async addReview() {
      await reviewService.add({
        toyId: this.toy._id,
        content: this.review.txt,
      });
      await this.$store.dispatch({ type: "loadToys" });
      await this.loadToy();
      this.review = { txt: "" };
    },
  },
  watch: {
    "$route.params.toyId"(id) {
      console.log("Changed to", id);
      this.loadToy();
    },
  },
  created() {
    this.loadToy();
  },
  components: {
    chatPage,
  },
};
</script>

