<template>
  <div class="container">
    <h2>Chat Room</h2>
    <!-- <label>
      <input type="radio" value="Politics" v-model="topic" @change="changeTopic" /> 
      Politics
    </label>
    <label>
      <input type="radio" value="Love" v-model="topic" @change="changeTopic" /> 
      Love
    </label> -->
    <ul>
      <li v-for="(msg, idx) in msgs" :key="idx">
        <p>from {{ msg.from }}: {{ msg.txt }}</p>
      </li>
    </ul>
    <p v-if="isUserTyping">{{this.userTyping}} is typing</p>
    <form @submit.prevent="sendMsg">
      <input type="text" v-model="msg.txt" @input="sendUserTyping" />
      <button>Send</button>
    </form>
  </div>
</template>

<script>
import { socketService } from "@/services/socket.service";
import { userService } from "../services/user.service";

export default {
  props: {
    toy: Object,
  },
  data() {
    return {
      fromUser: userService.getLoggedInUser(),
      userTyping: null,
      msg: { from: this.fromUser, txt: "" },
      msgs: [],
      topic: this.toy._id,
      isUserTyping: false,
    };
  },
  created() {
    this.fromUser = userService.getLoggedInUser();
    this.msg = { from: this.fromUser, txt: "" };

    socketService.setup();
    socketService.emit("chat topic", this.toy._id);
    socketService.on("chat addMsg", this.addMsg);

    socketService.on("user is typing", this.showTyping);
  },
  destroyed() {
    socketService.off("chat addMsg", this.showTyping);
    socketService.terminate();
  },
  methods: {
    debounce(callback, wait, immediate = false) {
      let timeout = null;

      return function () {
        const callNow = immediate && !timeout;
        const next = () => callback.apply(this, arguments);

        clearTimeout(timeout);
        timeout = setTimeout(next, wait);

        if (callNow) {
          next();
        }
      };
    },
    showTyping(fromUser) {
      this.isUserTyping=true;
      this.userTyping=fromUser;
      setTimeout(() => {
        this.isUserTyping=false
        this.userTyping=null
      }, 2000)
    },
    addMsg(msg) {
      this.msgs.push(msg);
    },
    sendMsg() {
      console.log("Sending", this.msg);
      socketService.emit("chat newMsg", this.msg);
      this.msg = { from: this.fromUser, txt: "" };
    },
    changeTopic() {
      socketService.emit("chat topic", this.topic);
    },
    sendUserTyping() {
      socketService.emit("user typing", this.fromUser);
    },
  },
  // created(){
  //
  // }
};
</script>
